import React from "react";
import image1 from "../img/category-1.jpg";
import image2 from "../img/category-2.jpg";
import image3 from "../img/category-3.jpg";
import image4 from "../img/category-4.jpg";



function ShopPage(){


    return (
        <>
        <div className="shopnow-div">
            {/* First Column */}
            <div className="shop-box">
            <div className="shop-box-1">
            <div style={{ backgroundImage:`url(${image1})`, backgroundSize:'cover',backgroundRepeat: 'no-repeat',
            height:'250px',width:'350px',display:'flex',
            justifyContent:'flex-end',flexDirection:'column'}}>
            <button className="category-btn">Fruits</button>
            </div>
            </div>

                <br></br>

            <div className="shop-box-2">
            <div style={{ backgroundImage:`url(${image2})`, backgroundSize:'cover',backgroundRepeat: 'no-repeat',
            height:'250px',width:'350px',display:'flex',
            justifyContent:'flex-end',flexDirection:'column'}}>
            <button className="category-btn">Vegetables</button>
            </div>
            </div>
            </div>

            {/* Second Column */}
            <div className="shop-box-mid">
                <h3>Vegetables</h3>
                <label>Protect the health of every home</label>
                <br></br>
                <button className="category-shop-btn">Shop now</button>
            

            </div>
            
            {/* Third Column */}
            <div className="shop-box">
            <div className="shop-box-3">
            <div style={{ backgroundImage:`url(${image3})`, backgroundSize:'cover',backgroundRepeat: 'no-repeat',
            height:'250px',width:'350px',display:'flex',
            justifyContent:'flex-end',flexDirection:'column'}}>
            <button className="category-btn">Juices</button>
            </div>
            </div>

                <br></br>

            <div className="shop-box-4">
            <div style={{ backgroundImage:`url(${image4})`, backgroundSize:'cover',backgroundRepeat: 'no-repeat',
            height:'250px',width:'350px',display:'flex',
            justifyContent:'flex-end',flexDirection:'column'}}>
            <button className="category-btn">Dried</button>
            </div>
            </div>

            </div>
        </div>
        </>
    )
}

export default ShopPage;