import React, { useContext, useEffect, useReducer } from "react";
// import data from "../data";
import axios from "axios"
import { Link } from "react-router-dom";


const reducer = (state, action) => {
    switch(action.type) {
        case 'FETCH_REQUEST':
        return {...state, loading: true};
        case 'FETCH_SUCCESS':
        return {...state, products: action.payload, loading:false};
        case 'FETCH_FAIL':
        return {...state, loading: false, error: action.payload};
        default:
        return state;
    }
};

function ProductPage(){

const[{loading, error, products}, dispatch] = useReducer(reducer, {
    products: [],
loading:true,
error: '',
});


// const [products, setProducts] = useState([]);
useEffect(()=>{
    const fetchData = async () => {
        dispatch({type: 'FETCH_REQUEST'});
        try {
            const result = await axios.get('/api/products');
            dispatch({type:'FETCH_SUCCESS', payload: result.data})
        } catch(err) {
            dispatch({type:'FETCH_FAIL', payload: err.message});

        }
        
        // setProducts(result.data);
    };
    fetchData();
},[]);

// add o cart
// const {state, dispatch: cxtDispatch} = useContext();
// const addToCartHandler = () => {
//     cxtDispatch({type:'CART_ADD_ITEMS',
//     payload: {...products, quantity:1}
// });
// };

    return (
        <>
        <div className="product-main-div">
        <div className="product-div_1">
        <h5>Featured Products</h5>
        <h1>Our Products</h1>
        <p>Far far away, behind the word mountains, far from the countries Vokalia and Consonantia</p>
        </div>
        <div className="product-div_2">
        {
            products.map(product =>(
            <div className="product" key={product.slug}>
                <Link to={`/products/${product.slug}`}>
            <img src={product.image} alt={product.name}/>
                </Link>
            <div className="product-info">
            <p> {product.name} </p>
            <p style={{color:'#82ae46'}}> <strong>{product.price}</strong></p>
            <button>Add To Cart</button>
            </div>
            </div>))
        }
        </div>
        </div>
        </>
    )
}

export default ProductPage;